* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  box-sizing: border-box;
  cursor: pointer;
}
html {
  height: 100%;
  background-color: #F7F7F7;
}
html body {
  position: relative;
  min-width: 320px;
  max-width: 1024px;
  height: 100%;
  line-height: 1.5;
  color: #000;
  background-color: #FFF;
  margin: 0 auto;
  overflow: hidden;
}
html body :global(#root) {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
:global(.textFontSize) {
  font-size: 12px;
}
:global(.normalFontSize) {
  font-size: 14px;
}
:global(.titleFontSize) {
  font-size: 16px;
}
@media screen and (max-width: 321px) {
  :global(.textFontSizeC) {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  :global(.textFontSizeC) {
    /*中*/
    font-size: 13px;
