:global(.oneRowOverflowOmit) {
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
:global(.oneRowsOverflowOmit) {
  overflow: hidden;
  display: -webkit-box;
