:global(.carousel_common) :global(.carousel_common_item) {
  display: inline-block;
  width: 100%;
  max-height: 500px;
}
:global(.carousel_common) :global(.carousel_common_item) img {
  width: 100%;
  height: 4.5rem;
  max-height: 500px;
  vertical-align: top;
}
:global(.xBoth1px) {
  position: relative;
}
:global(.xBoth1px)::before,
:global(.xBoth1px)::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
:global(.xBoth1px)::before {
  top: 0;
}
:global(.xBoth1px)::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  :global(.xBoth1px)::before,
  :global(.xBoth1px)::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  :global(.xBoth1px)::before,
  :global(.xBoth1px)::after {
    transform: scaleY(0.33);
  }
}
